<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="MT_fn_btn"></div>

      <div class="MT_table_cont">
        <!--  -->
        <div class="btns">
          <el-button class="add_btn" @click.stop="addColumn" icon="el-icon-plus"></el-button>
          <!-- <div class="btn_cont">
            <div class="choose_btn _color">
              <span @click.stop="batchGroup"></span>
            </div>
          </div> -->
        </div>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="name" label=""> </el-table-column>
          <el-table-column prop="position" label="">
            <template slot-scope="scope">
              {{ scope.row.position === 1 ? '' : '' }}
            </template>
          </el-table-column>
          <el-table-column prop="orderNo" label=""> </el-table-column>
          <el-table-column prop="showType" label="">
            <template slot-scope="scope">
              {{ scope.row.showType === 1 ? 'download' : scope.row.showType === 2 ? '' : '' }}
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)"></el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)"></el-link>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)"></span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)"></span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryForm: {
        menuType: 1,
        total: '',
        page: 1,
        size: 10
      },
      tableData: []
    }
  },

  methods: {
    // 
    addColumn() {
      this.$router.push({ path: '/editColumn', name: 'EditColumn', params: { type: 'add' } })
    },

    // 
    edit(item) {
      this.$router.push({ path: '/editColumn', name: 'EditColumn', params: { type: 'edit', info: item } })
      this.$sessionData('set', 'columnInfo', item)
    },

    // 
    del(id) {
      console.log(id)
      this.$confirm(', ?', '', { type: 'warning' })
        .then(() => {
          this.httpPost('/itemset/rem/column/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/itemset/query/', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
