<template>
  <div>
    <!--  -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/column' }"></el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'add' ? '' : '' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <!--<el-form-item label="" prop="position">-->
          <!--<el-select v-model="ruleForm.position" placeholder="">-->
            <!--<el-option label="" value="1"></el-option>-->
            <!--<el-option label="" value="2"></el-option>-->
          <!--</el-select>-->
        <!--</el-form-item>-->
        <el-form-item label="" prop="orderNo">
          <el-input v-model="ruleForm.orderNo" @keyup.native="proving" placeholder=""></el-input>
        </el-form-item>
        <!--<el-form-item label="" prop="showType">-->
          <!--<el-select v-model="ruleForm.showType" placeholder="">-->
            <!--<el-option label="download" :value="1"></el-option>-->
            <!--<el-option label="" :value="2"></el-option>-->
            <!--<el-option label="" :value="3"></el-option>-->
          <!--</el-select>-->
        <!--</el-form-item>-->
        <el-form-item label="" prop="content">
          <Tinymce style="width: 60%" ref="bzlc" :id="'tinymceBzlc'" @getCont="getCont"></Tinymce>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="cancelForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import Tinymce from '@/components/Tinymce/Tinymce'
export default {
  components: { Tinymce },
  data() {
    return {
      ruleForm: {
        name: '',
        position: '1',
        orderNo: '',
        showType: '2',
        delivery: false,
        content: '',
        menuType: 1
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
        ],
        position: [{ required: true, message: '', trigger: 'change' }],
        orderNo: [{ required: true, message: '', trigger: 'change' }],
        showType: [{ required: true, message: '', trigger: 'change' }],
        content: [{ required: true, message: '', trigger: 'change' }]
      }
    }
  },

  methods: {
    // 
    getCont(val) {
      this.ruleForm.content = val
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.$route.params.type === 'add') {
            this.httpPost('/itemset/add/column', this.ruleForm).then(res => {
              this.$message.success('')
              this.cancelForm('ruleForm')
            })
          } else {
            this.httpPost('/itemset/mod/column/' + this.ruleForm.id, this.ruleForm).then(res => {
              this.$message.success('')
              this.cancelForm('ruleForm')
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    cancelForm(formName) {
      this.$refs[formName].resetFields()
      this.$router.back()
    },
    proving() {
      this.ruleForm.orderNo = this.ruleForm.orderNo.replace(/[^\.\d]/g, '')
      this.ruleForm.orderNo = this.ruleForm.orderNo.replace('.', '')
    }
  },

  mounted() {
    const { params } = this.$route
    if (params && params.type === 'edit') {
      var info = this.$sessionData('get', 'columnInfo')
      this.ruleForm = info
      this.$refs.bzlc.editorValue = info.content
      this.ruleForm.position = this.ruleForm.position.toString()
    }
  }
}
</script>

<style lang="scss" scoped>
/deep/ .el-input input {
  width: 400px;
}
</style>
