<template>
  <div>
    <!--  -->
    <el-card>
      <div class="main_cont" v-if="pageType === 0">
        <!--  -->
        <div class="MT_fn_btn">
          <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
            <el-form-item>
              <el-input v-model="queryForm.title" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click.stop="onSubmit"></el-button>
            </el-form-item>
          </el-form>
          <div class="btn_left"></div>
        </div>

        <div class="MT_table_cont">
          <!--  -->
          <div class="btns">
            <el-button v-if="$route.path !== '/message'" @click.stop="add" icon="el-icon-plus"></el-button>
            <div class="btn_cont">
              <div class="choose_btn _color">
                <span v-if="$route.path === '/message'"  @click.stop="removeMessage"></span>
                <span v-else @click.stop="remove"></span>
                <span v-if="$route.path !== '/message'" @click.stop="push"></span>
              </div>
            </div>
          </div>
          <!--  -->
          <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55"> </el-table-column>
            <el-table-column prop="rownum_" label="" width="100"> </el-table-column>
            <el-table-column v-if="$route.path === '/message'" prop="name" label="" width="180"> </el-table-column>
            <el-table-column prop="title" label=""> </el-table-column>
            <el-table-column v-if="$route.path !== '/message'" prop="hits" label="" width="120"> </el-table-column>
            <el-table-column v-else label="" width="120">
              <template slot-scope="scope">
                {{ scope.row.status === 0 ? '' : '' }}
              </template>
            </el-table-column>
            <el-table-column label="" width="120">
              <template slot-scope="scope">
                {{ scope.row.ifPublish === 1 ? '' : '' }}
              </template>
            </el-table-column>
            <el-table-column v-if="$route.path !== '/message'" prop="createDate" label="" width="180"> </el-table-column>
            <el-table-column v-else prop="createDate" label="" width="180"> </el-table-column>
            <!--  -->
            <el-table-column label="" width="100">
              <template slot-scope="scope">
                <el-link v-if="$route.path === '/message'" :underline="false" type="primary" @click.stop="edit(scope.row)"></el-link>
                <el-link v-else :underline="false" type="primary" @click.stop="edit(scope.row)"></el-link>
                <el-link v-if="$route.path === '/message'" :underline="false" type="danger" @click.stop="delMessage(scope.row.id)"></el-link>
                <el-link v-else :underline="false" type="danger" @click.stop="del(scope.row.id)"></el-link>
              </template>
            </el-table-column>
          </el-table>

          <!--  -->
          <div class="MT_pagination" v-if="queryForm.total">
            <span class="jump_first" @click.stop="$jumpToFirst(queryForm, changePage)"></span>
            <span class="jump_last" @click.stop="$jumpToLast(queryForm, changePage)"></span>
            <el-pagination
              background
              :total="queryForm.total"
              :current-page.sync="queryForm.page"
              :page-size.sync="queryForm.size"
              @current-change="changePage"
              layout="total, prev, pager, next, jumper"
            />
          </div>
        </div>
      </div>

      <!-- / -->
      <add-edit v-if="pageType === 1 || pageType === 2" :pageType="pageType" :info="currentInfo" @changePageType="changePageType" :type="type"/>

      <!--  -->
      <el-dialog title="" :visible.sync="dialogFormVisible" width="30%">
        <el-divider></el-divider>
        <el-form :model="form">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="form.region" placeholder="">
              <el-option :label="item.pushName" :value="item.id" v-for="(item, index) in platformList" :key="index"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="pushStand"> </el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import AddEdit from '@/components/add-edit/AddEdit'
export default {
  components: { AddEdit },
  props: ['pageId', 'dataInfo', 'queryForm','type'], // 0  1  2  3 
  data() {
    return {
      tableData: [],
      multipleSelection: [],
      pageType: 0, //  0  1  2  3 
      currentInfo: '',
      dialogFormVisible: false,
      form: {
        region: ''
      },
      formLabelWidth: '120px',
      platformList: []
    }
  },

  methods: {
    pushStand() {
      let bulletinIdList =[]
      for(var i = 0;i<this.multipleSelection.length;i++){
        bulletinIdList.push(this.multipleSelection[i].id)
      }
      let params = {
        bulletinIdList:bulletinIdList,
        pushPlatformId:this.form.region
      }

      this.httpPost('/push/bulletin/add', params).then(ret => {
        this.$message({ type: 'success', message: '!' })
        this.$emit('loadData')
        this.dialogFormVisible = false
      })
    },
    platform() {

      this.httpGet('/push/platform/queryAll', '').then(res => {
        this.platformList = res
      })
    },
    // 
    add() {
      this.pageType = 1
    },

    // 
    remove() {
      if (!this.multipleSelection.length) return this.$message.warning('')
      this.$confirm(', ?', '', { type: 'warning' })
        .then(() => {
          let idList =[]
          for(var i = 0;i<this.multipleSelection.length;i++){
            idList.push(this.multipleSelection[i].id)
          }
          this.httpPost('/bulletin/rem/batch', idList).then(ret => {
            this.$message({ type: 'success', message: '!' })
            this.$emit('loadData')
          })
        })
        .catch(() => {})
    },
    //
    removeMessage(){
      if (!this.multipleSelection.length) return this.$message.warning('')
      this.$confirm(', ?', '', { type: 'warning' })
        .then(() => {
          let idList =[]
          for(var i = 0;i<this.multipleSelection.length;i++){
            idList.push(this.multipleSelection[i].id)
          }
          this.httpPost('/message/rem', idList).then(ret => {
            this.$message({ type: 'success', message: '!' })
            this.$emit('loadData')
          })
        })
        .catch(() => {})
    },

    // 
    push() {
      if (!this.multipleSelection.length) return this.$message.warning('')
      this.dialogFormVisible = true
    },

    // 
    edit(item) {
      this.pageType = 2
      this.currentInfo = item
    },
    //
    delMessage(id){
      console.log(id)
      this.$confirm(', ?', '', { type: 'warning' })
        .then(() => {
          let idList =[]
          idList.push(id)
          this.httpPost('/message/rem', idList).then(ret => {
            this.$message({ type: 'success', message: '!' })
            this.$emit('loadData')
          })
        })
        .catch(() => {})
    },
    // 
    del(id) {
      console.log(id)
      this.$confirm(', ?', '', { type: 'warning' })
        .then(() => {
          this.httpPost('/bulletin/rem/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '!' })
            this.$emit('loadData')
          })
        })
        .catch(() => {})
    },

    changePageType(type) {
      this.pageType = type
      this.$emit('loadData')
    },

    // 
    changePage() {
      this.$emit('getPage', this.queryForm.page)
    },

    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    // 
    onSubmit() {
      this.$emit('getTitle', this.queryForm.title)
    }
  },

  watch: {
    dataInfo(newVal, oldVal) {
      this.tableData = newVal
    },

    pageType(newVal, oldVal) {
      this.$emit('getPageType', newVal)
    }
  },
  mounted() {
    this.platform()
  }
}
</script>

<style lang="scss" scoped></style>
